<template>
	<div id="deliveryer-guide">
		<public-header title="配送员详情"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-cell is-link :to="util.getUrl({path: '/pages/deliveryer/profile', query: {id: id}})">
					<i class="icon icon-profilefill c-info" slot="icon"></i>
					<div slot="title" >个人信息</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/deliveryer/power', query: {id: id}})">
					<i class="icon icon-waimai c-danger" slot="icon"></i>
					<div slot="title">配送权限</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/deliveryer/comision', query: {id: id}})">
					<i class="icon icon-moneybagfill c-primary" slot="icon"></i>
					<div slot="title">提成及提现</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t" v-if="0">
				<van-cell is-link :to="util.getUrl({path: '/pages/deliveryer/getcash', query: {id: id}})">
					<i class="icon icon-sponsorfill c-danger" slot="icon"></i>
					<div slot="title">提现申请</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/deliveryer/current', query: {id: id}})">
					<i class="icon icon-newshotfill c-info" slot="icon"></i>
					<div slot="title">账户明细</div>
				</van-cell>
			</van-cell-group>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				id: 0,
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		}
	}
</script>

<style>
	#deliveryer-guide .van-cell .icon{
		margin-right: 7px;
		font-size: 18px;
	}
</style>


